:root {
  --color-blue-button: rgb(83, 146, 249);
  --color-blue-button-hover: rgba(83, 146, 249, 0.8);
  --color-gray-icon: #8c8b8b;
  --color-red: rgb(225, 45, 45);
  --color-text-root: #333;
  --color-gray-600: #344054;
  --color-primary-50: #F2FFF9;
  --color-primary-500: #10975B;
}

.wrap-not-found {
  /* animations */
}
.wrap-not-found .cls-1 {
  fill: #ffc541;
}
.wrap-not-found .cls-2 {
  fill: #be1128;
}
.wrap-not-found .cls-3 {
  fill: #be1128;
}
.wrap-not-found .cls-4 {
  fill: #f78d5e;
}
.wrap-not-found .cls-5 {
  fill: #fa976c;
}
.wrap-not-found .cls-6,
.wrap-not-found .cls-7,
.wrap-not-found .cls-8 {
  fill: #b65c32;
}
.wrap-not-found .cls-10,
.wrap-not-found .cls-6 {
  opacity: 0.6;
}
.wrap-not-found .cls-7 {
  opacity: 0.4;
}
.wrap-not-found .cls-9 {
  fill: #f4b73b;
}
.wrap-not-found .cls-11 {
  fill: #f9c358;
}
.wrap-not-found .cls-12 {
  fill: #9b462c;
}
.wrap-not-found .cls-13 {
  fill: #aa512e;
}
.wrap-not-found .cls-14 {
  fill: #be1128;
}
.wrap-not-found .wheel {
  animation: wheel-rotate 6s ease infinite;
  transform-origin: center;
  transform-box: fill-box;
}
@keyframes wheel-rotate {
  50% {
    transform: rotate(360deg);
    animation-timing-function: cubic-bezier(0.55, 0.085, 0.68, 0.53);
  }
  100% {
    transform: rotate(960deg);
  }
}
.wrap-not-found .clock-hand-1 {
  animation: clock-rotate 3s linear infinite;
  transform-origin: bottom;
  transform-box: fill-box;
}
.wrap-not-found .clock-hand-2 {
  animation: clock-rotate 6s linear infinite;
  transform-origin: bottom;
  transform-box: fill-box;
}
@keyframes clock-rotate {
  100% {
    transform: rotate(360deg);
  }
}
.wrap-not-found #box-top {
  animation: box-top-anim 2s linear infinite;
  transform-origin: right top;
  transform-box: fill-box;
}
@keyframes box-top-anim {
  50% {
    transform: rotate(-5deg);
  }
}
.wrap-not-found #umbrella {
  animation: umbrella-anim 6s linear infinite;
  transform-origin: center;
  transform-box: fill-box;
}
@keyframes umbrella-anim {
  25% {
    transform: translateY(10px) rotate(5deg);
  }
  75% {
    transform: rotate(-5deg);
  }
}
.wrap-not-found #cup {
  animation: cup-rotate 3s cubic-bezier(0.455, 0.03, 0.515, 0.955) infinite;
  transform-origin: top left;
  transform-box: fill-box;
}
@keyframes cup-rotate {
  50% {
    transform: rotate(-5deg);
  }
}
.wrap-not-found #pillow {
  animation: pillow-anim 3s linear infinite;
  transform-origin: center;
  transform-box: fill-box;
}
@keyframes pillow-anim {
  25% {
    transform: rotate(10deg) translateY(5px);
  }
  75% {
    transform: rotate(-10deg);
  }
}
.wrap-not-found #stripe {
  animation: stripe-anim 3s linear infinite;
  transform-origin: center;
  transform-box: fill-box;
}
@keyframes stripe-anim {
  25% {
    transform: translate(10px, 0) rotate(-10deg);
  }
  75% {
    transform: translateX(10px);
  }
}
.wrap-not-found #bike {
  animation: bike-anim 6s ease infinite;
}
@keyframes bike-anim {
  0% {
    transform: translateX(-1300px);
  }
  50% {
    transform: translateX(0);
    animation-timing-function: cubic-bezier(0.47, 0, 0.745, 0.715);
  }
  100% {
    transform: translateX(1300px);
  }
}
.wrap-not-found #rucksack {
  animation: ruck-anim 3s linear infinite;
  transform-origin: top;
  transform-box: fill-box;
}
@keyframes ruck-anim {
  50% {
    transform: rotate(5deg);
  }
}
.wrap-not-found .circle {
  animation: circle-anim ease infinite;
  transform-origin: center;
  transform-box: fill-box;
  perspective: 0px;
}
.wrap-not-found .circle.c1 {
  animation-duration: 2s;
}
.wrap-not-found .circle.c2 {
  animation-duration: 3s;
}
.wrap-not-found .circle.c3 {
  animation-duration: 1s;
}
.wrap-not-found .circle.c4 {
  animation-duration: 1s;
}
.wrap-not-found .circle.c5 {
  animation-duration: 2s;
}
.wrap-not-found .circle.c6 {
  animation-duration: 3s;
}
@keyframes circle-anim {
  50% {
    transform: scale(0.2) rotateX(360deg) rotateY(360deg);
  }
}
.wrap-not-found .four,
.wrap-not-found #ou {
  animation: four-anim cubic-bezier(0.39, 0.575, 0.565, 1) infinite;
}
.wrap-not-found .four.a {
  transform-origin: bottom left;
  animation-duration: 3s;
  transform-box: fill-box;
}
.wrap-not-found .four.b {
  transform-origin: bottom right;
  animation-duration: 3s;
  transform-box: fill-box;
}
.wrap-not-found #ou {
  animation-duration: 6s;
  transform-origin: center;
  transform-box: fill-box;
}
@keyframes four-anim {
  50% {
    transform: scale(0.98);
  }
}